import React, { Component } from 'react';

import Styles from './styles';

import { Grid, Column, OptusButton, Ribbon, Row } from '../../lib-web';
import Visa from './images/visa.png';
import Mastercard from './images/mastercard.png';
import Diners from './images/diners.png';
import Amex from './images/amex.png';

const Brands = {
  Visa: 'Visa',
  Amex: 'American Express',
  MasterCard: 'Master Card',
  Diners: 'Diners'
};

class SavedCardForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      errorDetails: '',
      validityMonth: '',
      validityYear: '',
      imgUrl: ''
    };
    this.expiredCard = false;

    this.addPaymentMethod = this.addPaymentMethod.bind(this);
    this.checkExpiredCard = this.checkExpiredCard.bind(this);
  }

  addPaymentMethod() {}

  componentDidMount() {
    this.identifyImage();
    this.checkExpiredCard();
  }

  checkExpiredCard() {
    if (
      this.props.data.validityYear <=
        new Date()
          .getFullYear()
          .toString()
          .substr(-2) &&
      this.props.data.validityMonth < new Date().getMonth() + 1
    ) {
      this.expiredCard = true;
    }
  }

  identifyImage() {
    var url = '';
    switch (this.props.data.brand) {
      case Brands.Visa:
        url = Visa;
        break;
      case Brands.Amex:
        url = Amex;
        break;
      case Brands.MasterCard:
        url = Mastercard;
        break;
      case Brands.Diners:
        url = Diners;
        break;
    }
    this.setState({ imgUrl: url });
  }

  render() {
    return (
      <div>
        <Grid>
          <Row>
            <Column sm={12} md={8} lg={8}>
              <Styles.DisplaySavedCards>
                <div className="ux-card-data">
                  {this.props.isDefaultPayMethod === false ? (
                    ''
                  ) : (
                    <Ribbon mountTo="left">
                      {' '}
                      {this.props.directDebitHeader}{' '}
                    </Ribbon>
                  )}
                  <div className="standard">
                    <div className="card-details-wrapper">
                      <img src={this.state.imgUrl} alt="Card" />
                      <div className="card-details">
                        <div className="card-type-wrapper">
                          <div className="card-type">
                            {this.props.data.brand} card ending{' '}
                            {this.props.data.lastFourDigits}
                          </div>
                        </div>
                        {this.expiredCard === false ? (
                          <div className="expiry">
                            Expiry date :{' '}
                            {this.props.data.validityMonth &&
                            this.props.data.validityMonth.length > 1
                              ? this.props.data.validityMonth
                              : '0' + this.props.data.validityMonth}/{
                              this.props.data.validityYear
                            }
                          </div>
                        ) : (
                          <div className="expiry savedCardExpired">
                            Expiry date :{' '}
                            {this.props.data.validityMonth.length > 1
                              ? this.props.data.validityMonth
                              : '0' + this.props.data.validityMonth}/{
                              this.props.data.validityYear
                            }
                          </div>
                        )}

                        <br />
                        {this.expiredCard === false ? (
                          ''
                        ) : (
                          <div>
                            <div className="savedCardExpired">
                              {this.props.expiredCardMessage}
                            </div>
                            <br />
                          </div>
                        )}

                        {this.props.isDefaultPayMethod === true ? (
                          ''
                        ) : this.expiredCard === true ? (
                          ''
                        ) : (
                          <div>
                            <a
                              className="useAsDirectDebit"
                              href="#"
                              role="link"
                              aria-label="Use as direct debit"
                              tabIndex="0"
                            >
                              {this.props.useAsDirectDebitLabel}
                            </a>
                          </div>
                        )}
                      </div>
                    </div>
                    <div className="ctas">
                      <a
                        className="ux-anchor remove-link change-link "
                        href="#removeCardModal"
                        role="link"
                        aria-label="Remove card"
                        tabIndex="0"
                      >
                        Remove
                      </a>
                    </div>
                  </div>
                </div>
              </Styles.DisplaySavedCards>
            </Column>
          </Row>
        </Grid>
        <br />
      </div>
    );
  }
}

export default SavedCardForm;
