import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Styles from './styles';
import { OptusButton, Grid, Row, Column } from '../../lib-web';
import SavedCardForm from './SavedCardForm';
import SavedBankAccountForm from './SavedBankAccountForm';
import DirectDebitService from '../../amws-core/services/DirectDebitService';

class Wallet extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showPaymentMethod: false,
      savedPaymentMethod: false,
      cards: [],
      bankAccounts: [],
      payDetails: {}
    };

    this.axios = require('axios');
    this.addPaymentMethod = this.addPaymentMethod.bind(this);
    this.ddService = new DirectDebitService();
    this.addNewCardAllowed = false;
  }

  addPaymentMethod() {
    this.setState({
      showPaymentMethod: true
    });
  }

  getSavedPaymentMethods() {
    this.ddService
      .getSavedPaymentMethods(this.props.isLocal)
      .then(response => {
        response = response.data;
        if (response && response.PayMeans) {
          if (response.PayMeans.card) {
            this.setState({ savedPaymentMethod: false });
            let paymentCardDetails = response.PayMeans.card;
            if (paymentCardDetails && paymentCardDetails.length > 0) {
              this.setState({ cards: paymentCardDetails });
            }
            let paymentBankDetails = response.PayMeans.bankAccount;
            if (paymentBankDetails && paymentBankDetails.length > 0) {
              this.setState({ bankAccounts: paymentBankDetails });
            }
          }
        }
      })
      .catch(error => {
        console.log(error);
      });
  }

  getDefaultPaymentMethod() {
    this.ddService
      .getDirectDebitStatus(this.props.isLocal)
      .then(response => {
        response = response.data;
        if (response && response.ImplCustomerRoleDetails) {
          if (
            response.ImplCustomerRoleDetails.accountDetails ||
            response.ImplCustomerRoleDetails.cardDetails
          ) {
            this.setState({ directDebitStatus: this.props.statusActiveText });
            var payDetails = response.ImplCustomerRoleDetails.cardDetails
              ? response.ImplCustomerRoleDetails.cardDetails
              : response.ImplCustomerRoleDetails.accountDetails;
            this.setState({ payDetails: payDetails, savedPaymentMethod: true });
          } else {
            this.setState({ savedPaymentMethod: false });
          }
        }
      })
      .catch(error => {
        this.setState({
          directDebitStatus: this.props.errorMessage
        });
      });
  }

  componentDidMount() {
    this.getDefaultPaymentMethod();
    this.getSavedPaymentMethods();
  }

  render() {
    return (
      <Grid>
        <Row>
          <Column sm={12} md={12} lg={12}>
            <Styles.Wallet>
              <h2>{this.props.walletHeader}</h2>
              <p>
                {this.props.walletInfoLabel}{' '}
                <a
                  className="ux-anchor change-link"
                  href={this.props.directDebitLink}
                >
                  {this.props.directDebitLinkText}
                </a>{' '}
                {this.props.walletPostInfoLabel}
              </p>
              <br />
              <br />
            </Styles.Wallet>
          </Column>
        </Row>
        <Row>
          <Column sm={12} md={12} lg={12}>
            <Styles.Wallet>
              {this.state.cards.map((item, key) => {
                return (
                  <SavedCardForm
                    key={item.id}
                    data={item}
                    isDefaultPayMethod={
                      this.state.payDetails.cardNumber === item.cardNumberX8
                        ? true
                        : false
                    }
                    addPaymentMethodLabel={this.props.addPaymentMethodLabel}
                    directDebitHeader={this.props.directDebitHeader}
                    expiredCardMessage={this.props.expiredCardMessage}
                  />
                );
              })}
              {this.state.bankAccounts.map((item, key) => {
                return (
                  <SavedBankAccountForm
                    key={item.id}
                    data={item}
                    isDefaultPayMethod={
                      this.state.payDetails.accountNumber === item.accountNumber
                        ? true
                        : false
                    }
                    addPaymentMethodLabel={this.props.addPaymentMethodLabel}
                    directDebitHeader={this.props.directDebitHeader}
                    bsbLabel={this.props.bsbLabel}
                    bankAccountNumberLabel={this.props.bankAccountNumberLabel}
                    useAsDirectDebitLabel={this.props.useAsDirectDebitLabel}
                  />
                );
              })}
              <br />
              {this.addNewCardAllowed === false ? (
                ''
              ) : (
                <div>
                  <OptusButton
                    className="addButton"
                    onClick={this.addPaymentMethod}
                  >
                    {this.props.addPaymentMethodLabel}
                  </OptusButton>
                </div>
              )}
            </Styles.Wallet>
          </Column>
        </Row>
      </Grid>
    );
  }
}

Wallet.defaultProps = {
  className: 'Wallet'
};

Wallet.propTypes = {
  /** Class name */
  className: PropTypes.node,
  /** Children inside component */
  children: PropTypes.node
};

export default Wallet;
