import React, { Component } from 'react';
import { Column, Grid, Ribbon, Row } from '../../lib-web';
import Styles from './styles';
import Wallet from './index';

class SavedBankAccountForm extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {}

  render() {
    return (
      <div>
        <Grid>
          <Row>
            <Column sm={12} md={8} lg={8}>
              <Styles.DisplaySavedCards>
                <div className="ux-card-data">
                  {this.props.isDefaultPayMethod === false ? (
                    ''
                  ) : (
                    <Ribbon mountTo="left">
                      {' '}
                      {this.props.directDebitHeader}{' '}
                    </Ribbon>
                  )}
                  <div className="standard">
                    <div className="card-details-wrapper">
                      <Styles.BankIcon />
                      <div className="card-details">
                        <div>
                          <span className="heading-bold">
                            {this.props.bsbLabel} :{' '}
                          </span>
                          <span className="heading-text">
                            {this.props.data.branchCode &&
                            this.props.data.branchCode.length > 1
                              ? this.props.data.branchCode
                              : ''}
                          </span>
                        </div>
                        <div>
                          <span className="heading-bold">
                            {this.props.data.accountName}
                          </span>
                          <span />
                        </div>
                        <div>
                          <span className="heading-bold">
                            {this.props.bankAccountNumberLabel}:{' '}
                          </span>
                          <span className="heading-text">
                            {this.props.data.accountNumber &&
                            this.props.data.accountNumber.length > 1
                              ? this.props.data.accountNumber
                              : ''}
                          </span>
                        </div>
                        {this.props.isDefaultPayMethod === true ? (
                          ''
                        ) : (
                          <div>
                            <br />

                            <a
                              className="useAsDirectDebit"
                              href="#"
                              role="link"
                              aria-label="Use as direct debit"
                              tabIndex="0"
                            >
                              {this.props.useAsDirectDebitLabel}
                            </a>
                          </div>
                        )}
                        <br />
                      </div>
                    </div>
                    <div className="ctas">
                      <a
                        className="ux-anchor remove-link change-link "
                        href="#removeCardModal"
                        role="link"
                        aria-label="Remove card"
                        tabIndex="0"
                      >
                        Remove
                      </a>
                    </div>
                  </div>
                </div>
              </Styles.DisplaySavedCards>
            </Column>
          </Row>
        </Grid>
      </div>
    );
  }
}

SavedBankAccountForm.defaultProps = {
  className: 'Wallet',
  isDefaultPayMethod: false,
  bsbLabel: 'BSB'
};
export default SavedBankAccountForm;
